<script>
export default {
  name:"",
  data() {
    return{
    input1:'',
    input2:'',
    input3:'',
    second:1.5,
    minutes:60,
    num: '',
    RyouekiData: [
      { id: 1, name:"", human:0},
      { id: 2, name:"", human:0},
      { id: 3, name:"", human:0},
      { id: 4, name:"", human:0},
      { id: 5, name:"", human:0},
      { id: 6, name:"", human:0},
      { id: 7, name:"", human:0},
      { id: 8, name:"", human:0},
      { id: 9, name:"", human:0},
      { id: 10, name:"", human:0},
      { id: 11, name:"", human:0},
      { id: 12, name:"", human:0},
      { id: 13, name:"", human:0},
      { id: 14, name:"", human:0},
      { id: 15, name:"", human:0},
      { id: 16, name:"", human:0},
      { id: 17, name:"", human:0},
      { id: 18, name:"", human:0},
      { id: 19, name:"", human:0},
      { id: 20, name:"", human:0},
      { id: 21, name:"", human:0},
      { id: 22, name:"", human:0},
    ]
    }
  },
  computed:{
    multi() {
      return (Math.round(Math.round((this.input1 * this.minutes + this.input2) / this.second * this.input3) / 2) * 2)
    },
    totalhuman() {
      let total = 0
      const length = this.RyouekiData.length
      for(let i = 0; i < length; i++) {
        total += this.RyouekiData[i].human
      }
      return Math.round(total) 
    }
  },
  methods: {
    show(nu) {
      this.num = this.RyouekiData[nu]
      this.$modal.show('hello-world');
    },
    hide() {
      this.num.human = this.multi
      this.$modal.hide('hello-world');
    },
    resethuman() {
      this.RyouekiData.forEach(element => {
        element.human = 0
      });
    }
  }
}
</script>

